import React, {useState} from 'react';
import './App.css';
function CookieBanner() {
const [cookieAccepted, acceptCookies] = useState(false);

const accept = () => {
acceptCookies(true);
console.log('cookieAccepted now: true');
}


return(
    <div id='cookies' hidden={cookieAccepted}>
        <h3 id='cookieText'>By using this website, you agree to the use of cookies to improve your experience and crypto mining so we won't have to show ads. <br/> <button onClick={accept} id='cookieButton'>Click here to accept.</button></h3>
    </div>
)
}
export default CookieBanner;