import React, {useState, useCallback} from 'react';
import './DumbThings.css';
function DumbThings() {

    const [dumbActions, setDumbActions] = useState(0);
    const [dumbPoints, setDumbPoints] = useState(0);
    const [message, setMessage] = useState("");
    const [messageVisibility, setMessageVisibility] = useState(true);
    const [reset, doReset] = useState(false);
let i = 1;
    const debug = () => {
        console.log(i);
        i++;
    }
const virusDownload = useCallback(() => {
    let emailPrompt = window.prompt("Please enter your username/email", "username");
    let pwdPrompt = window.prompt("Please enter your password", "password");
    if(emailPrompt !== null && emailPrompt !== "" && pwdPrompt !== null && pwdPrompt !== "") {
        if(window.confirm("would you like to install MalAntiVirus?")){
        window.alert("Now installing MalAntiVirus");
        setMessage("You just installed malware! Great Job! I hope you didn't put your real email/pwd (look below) +5 dumbPoints");
        setMessageVisibility(false); 
        setDumbPoints(dumbPoints + 5);
        setDumbActions(dumbActions + 1);
        }else{
            setMessageVisibility(false);
            setMessage("Please try again");
            virusDownload();
        }
}
}, [dumbActions, dumbPoints]);
const RAMDownload = useCallback(() => {
    if(window.confirm("Open link in new tab?")) {
        window.open('https://downloadmoreram.com/', '_blank');
        setMessage("More RAM Downloaded! BTW, that's just a joke site. +1 DP");
        setMessageVisibility(false);
        setDumbActions(dumbActions + 1);
        setDumbPoints(dumbPoints + 1);
    }
}, [dumbActions, dumbPoints])
const Learn2Learn = useCallback(() => {
    if(window.confirm("Open link in new tab?")) {
        window.open('https://www.youtube.com/watch?v=dQw4w9WgXcQ', '_blank');
        setMessage("LOL YOU FELL FOR IT! +1 DP");
        setMessageVisibility(false);
        setDumbActions(dumbActions + 1);
        setDumbPoints(dumbPoints + 1);
    }
}, [dumbActions, dumbPoints])
const closeMessage = () => {
    setMessageVisibility(true);
}//
return (
    <div id="DumbThings">
        <h1>Dumb Game Maker Made This Dumb Game</h1>
        <h4>The game where you do the stupidest things ever</h4>
        <h6>Don't worry, it doesn't actually download malware or anything :)</h6>
        <div id="banner">
            <p id="bannerText" hidden={messageVisibility}>
            {message}
            </p>
            <button hidden={messageVisibility} onClick={closeMessage}>
                Close
            </button>
        </div>
        <div className="actions">
            <button hidden={(reset || dumbPoints < 10)} onClick={doReset}>Reset for more?</button>
            <p hidden={reset}>VIRUS ALERT! CLICK BELOW TO ANTIVIRUS YOUR PC</p>
            <button hidden={reset} onClick={virusDownload} id="virusButton">CLICK ME</button><br /><br />
            <p hidden={reset}>Need more RAM? Click below to download them!</p>
            <button hidden={reset} onClick={RAMDownload} >DownloadMoreRAM</button><br /><br />
            <p hidden={reset}>Computer slow? Click below to download PC Optimizer Pro!</p>
            <button hidden={reset} onClick={virusDownload}>CLICK ME</button>
            <p hidden={reset}>Want to learn about how to learn?</p>
            <button hidden={reset} onClick={Learn2Learn}> YES! </button>
        </div>
        <div className="stats"> 
            <p>
                DP: {dumbPoints}
                <br />
                DA: {dumbActions}
            </p>
        </div>
    </div>
);
}
export default DumbThings;